import {
    ListItem,
    ListItemText,
    ListItemSecondaryAction,
    IconButton,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";

const Item = ({
    itemkey,
    name,
    description,
    link,
    price,
    bought = false,
    deleteClick,
    reserve,
    auth,
}) => {
    return (
        <ListItem button onClick={() => window.open(link, "_blank")}>
            <ListItemText
                id={itemkey}
                primary={`${name} ${description} ${price}, AM I BOUGHT? : ${bought}`}
            />
            <ListItemSecondaryAction>
                <IconButton edge="end" onClick={() => reserve(itemkey)}>
                    <ShoppingCartIcon />
                </IconButton>
                {auth && (
                    <>
                        <IconButton
                            edge="end"
                            onClick={() => deleteClick(itemkey, auth)}
                        >
                            <DeleteIcon />
                        </IconButton>
                    </>
                )}
            </ListItemSecondaryAction>
        </ListItem>
    );
};

export default Item;
